.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--light) !important;
  height: 100vh;
}

.bg-image {
  position: fixed;
  height: 100vh;
  width: 100%;
  background: linear-gradient(
    rgba(0, 0, 0, 0.3), 
    rgba(0, 0, 0, 0.3), 
    rgba(0, 0, 0, 0.3)
  ),url('../assets/images/engagement2.jpg'); /* Replace 'path/to/your-image.jpg' with the actual path to your image */
  background-size: cover; /* Cover the entire container with the background image */
  background-position: center; /* Center the background image horizontally and vertically */
  background-repeat: no-repeat; /* Prevent background image from repeating */
  color: var(--light); /* Optional: Set text color for better visibility */
}

.title-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 99999;
}

.title {
  font-family: var(--Allura) !important;
  font-size: 125px;
  font-weight: 400 !important;
  color: var(--satinsheetgold) !important;
  text-align: center;
}

.subtitle {
  font-family: var(--Garamond);
  font-size: 25px !important;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 99999;
  font-weight: 700;
}

.small {
  font-size: 25px !important;
  margin-top: 7px;
}

hr {
  -webkit-animation: lineDraw 2s;
  -moz-animation: lineDraw 2s;
  -o-animation: lineDraw 2s;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
  animation: lineDraw 2s;
  animation-fill-mode: forwards;
}

h1 {
  font-size: 67px;
  margin: 0px 15px 0px 0px !important;
  color: var(--purple)
}

h2 {
  font-size: 35px;
  letter-spacing: -1px !important;
  color: var(--grey);
  margin-bottom: 0px !important;
}

h3 {
  font-family: 'EB Garamond', serif;
  text-transform: lowercase;
  letter-spacing: 1px;
  font-size: 21px !important;
}

.logo-title {
  width: 55px;
  height: 125px;
  margin-right: 11px;
  animation: rotate 3s;
}

.RSVP-btn {
  margin-top: 45px;
  border: 2px solid; /* Set initial border to transparent */
  border-radius: 10px; /* Adjust as needed */
  padding: 10px; /* Adjust as needed */
  box-shadow: 0 0 7px var(--satinsheetgold); /* Set initial shadow color and size */
  transition: box-shadow 0.3s ease; /* Add transition effect */
  /* Additional styling */
  background-color: #00000000; /* Background color of the element */
  color: var(--satinsheetgold); /* Text color */
  transition: .2s ease-in-out;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 15px;
  padding: 15px;
}

.RSVP-btn:hover {
  transform: scale(1.1);
  cursor: pointer;
  color: white;
}


@keyframes lineDraw {
  0% {
    width: 0px;
  }
  100% {
    width: 65%;
  }
}


@media (min-width: 280px) and (max-width: 800px) {
  h2 {
    font-size: 25px;
  }
  h3 {
    font-size: 19px !important;
  }
  .title {
    font-size: 95px;
    margin-right: 5px !important;
    line-height: .7;
  }
  .subtitle {
    font-size: 19px !important;
  }

  .border {
    height: 95px !important;
    width: 95px !important;
  }

  @keyframes lineDraw {
    0% {
      width: 0px;
    }
    100% {
      width: 75%;
    }
  }
}


@media (min-width: 625px) and (max-width: 1024px) {
  h3 {
    font-size: 25px !important;
  }
  .title {
    font-size: 95px;
  }
  /* .title-area {
    margin-top: -50% !important;
  } */
  @keyframes lineDraw {
    0% {
      width: 0px;
    }
    100% {
      width: 65%;
    }
  }
}